<template>
    <el-dialog 
        title="通用语句" 
        width="60%" 
        :lock-scroll="true"
        :destroy-on-close="true"
        :append-to-body="true"
        v-model="dialogCopyVisible" 
        :before-close="handelcloseDialog">
        <div class="pub-table-mod">
            <div class="pub-table-searchbox">
                <div>
                    <el-input placeholder="项目"></el-input>
                </div>
                <div>
                    <el-input placeholder="情况"></el-input>
                </div>
                <div class="btn">
                    <el-button class="commonBtn primary" size="mini">查询</el-button>
                </div>
            </div>
            <el-table
                border
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                :header-cell-style="{background:'#efefe'}"
                @selection-change="handleSelectionChange">
                <el-table-column
                type="selection"
                width="55">
                </el-table-column>
                <el-table-column
                label="项目"
                width="120">
                <template #default="scope">{{ scope.row.date }}</template>
                </el-table-column>
                <el-table-column
                prop="name"
                label="情况"
                width="120">
                </el-table-column>
                <el-table-column
                prop="address"
                label="语句"
                show-overflow-tooltip>
                </el-table-column>
            </el-table>
        </div>
        <template #footer>
            <span class="dialog-footer">
                <el-button @click="handelcloseDialog">取 消</el-button>
                <el-button type="primary" @click="saveData">确 定</el-button>
            </span>
        </template>
    </el-dialog>
</template>
<script lang="ts">
import { defineComponent ,inject, reactive, toRefs } from 'vue'
export default defineComponent ({
    emits: {
        'copyData': (data: any) => {
            return data
        }
    },
    setup(props, context){
        const $api :any = inject('$api')
        const dataMap = reactive({
            dialogCopyVisible: Boolean<false>(),
            tableData: [{
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-08',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-06',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
                }, {
                date: '2016-05-07',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }],
            multipleSelection: Array<[]>()
        })
        const saveData = () => {
            context.emit('copyData', dataMap.multipleSelection)
            handelcloseDialog()
        }
        const handleSelectionChange = (val: any) => {
            // let dataArr:any = [] 
            // val.forEach((item: any) => {
            //     dataArr.push(item.name)
            // });
            dataMap.multipleSelection = val
        }
        const handelcloseDialog = () => {
            dataMap.dialogCopyVisible = false
        }
        return {
            ...toRefs(dataMap),
            handelcloseDialog,
            saveData,
            handleSelectionChange
        }
    }
})
</script>