<template>
    <div class="pub-form-bd">
        <div class="pub-form-item pub-form-border">
            <div class="pub-form-col-2 w100">
                <span class="label">{{descName}}</span>
                <div class="content content-vertical-box">
                    <el-input v-model="source.Comment" type="textarea" :rows="2" placeholder="诉讼记录备注"></el-input>
                    <span class="pub-form-icon el-icon-info add-icon-color info-icon" style="vertical-align:middle;" @click="openCommonWord"></span>
                    <div class="inline-box">                        
                        <el-checkbox v-model="source.noRecord" @change="getCommonWord" style="vertical-align:middle;">本次调查未发现该主体涉及有关记录。</el-checkbox>
                    </div>
                </div>
            </div>
            <div class="pub-form-col-2">
                <span class="label">全部数量</span>
                <div class="content">
                    <el-input v-model.number="source.AllLitigationCount" placeholder="全部数量"></el-input>
                </div>
            </div>
            <div class="pub-form-col-2">
                <span class="label">报告导出</span>
                <div class="content">
                    <el-select v-model="source.ExportLitigationCount" clearable filterable allow-create @change="checkNum" :class="{'danger': source.ExportLitigationCountErr !== '' && source.ExportLitigationCountErr !== undefined, 'w100': true}">
                        <el-option v-for="i in exportReportArr" :key="i.value" :label="i.label" :value="i.value"></el-option>
                    </el-select>
                </div>
            </div>
        </div>
        <CommonWord 
            ref="commonWords"
            @copyData="getWordData"
            />
    </div>
</template>
<script lang="ts">
import { defineComponent ,inject, reactive, toRefs, ref, onBeforeMount, watch  } from 'vue'
import CommonWord from '@/components/commonWord/index.vue'
import { isNumber } from '@/utils/public'
import { useStore } from '@/store'
export default defineComponent ({
    props: {
        source: {
			type: Object,
			default: () => {
                return {}
            }
		},
        componentName: {
            type: String,
            default: ''
        },
        descName: {
            type: String,
            default: ''
        }
    },
    components: {
        CommonWord
    },
    setup(props, context) {
        const commonWords = ref()
        const store = useStore()
        const $api :any = inject('$api')
        const dataMap = reactive({
            exportReportArr: [
                {
                    label: '请选择',
                    value: ''
                },
                {
                    label: '50',
                    value: '50'
                },
                {
                    label: '100',
                    value: '100'
                },
                {
                    label: '全部',
                    value: '全部'
                }
            ]
        })
        const checkNum = (val: any) => {
            if (!isNumber(val)) {
               props.source.ExportLitigationCountErr = '只能填数字'
            } else {
                props.source.ExportLitigationCountErr = ''
            }
        }
        const getCommonWord = (val: any) => {
            if (val) {
                props.source.Comment = '本次调查未发现该主体涉及有关记录。'
            } else {
                props.source.Comment = ''
            }
        }
        const openCommonWord = () => {
            commonWords.value.dialogCopyVisible = true
        }
        const getWordData = (data: any) =>  {
            console.log(data)
            props.source.Comment = data
        }
        onBeforeMount(() => {
            const dataList = store.state.app.data
            let commentData: any = {}
            let filterArr = dataList.filter((ele: any) => {
                if (ele.commentData) {
                    commentData = ele.commentData
                }
                return props.componentName === ele.components.name
            })
            props.source.ExportLitigationCount = commentData.ExportLitigationCount
            props.source.Comment = commentData.Comment
            props.source.noRecord = commentData.noRecord
            props.source.AllLitigationCount = commentData.AllLitigationCount
            props.source.ExportLitigationCount = props.source.ExportLitigationCount ? props.source.ExportLitigationCount : filterArr.length
        })
        return {
            ...toRefs(dataMap),
            checkNum,
            getCommonWord,
            openCommonWord,
            getWordData,
            commonWords,
            store
        }
    }
})
</script>
